import React, { useState } from 'react'
import '../../styles/layout/langSelect.css'
import { Link } from 'gatsby';

export default function LangSelect() {
    const [langState, setLangState] = useState('hide');
    const [defaultLang, setDefaultLang] = useState('brazil');
    const toggleLang = () => {
        if (langState === 'hide') setLangState('show')
        else setLangState('hide');
    }
    const englishLang = () => {
        setDefaultLang('english');
    }
    const brazilLang = () => {
        setDefaultLang('brazil');
    }
    return (
        <button onClick={toggleLang} className="lang-select">
            <img src={defaultLang === 'english' ? '/qkzclan/images/united-kingdom.png' : '/qkzclan/images/brazil.png'} alt="" />
            <p>{defaultLang === 'english' ? 'ENGLISH' : 'PORTUGUÊS'} ▼</p>
            <ul className={langState === 'hide' ? 'lang-options' : 'lang-options lang-show'}>
                <li className='lang-item'>
                    <Link onClick={englishLang} to='/'>
                        <img src="/qkzclan/images/united-kingdom.png" alt="" />
                        <p>ENGLISH</p>
                    </Link>
                </li>
                <li className='lang-item'>
                    <Link onClick={brazilLang} to='/'>
                        <img src="/qkzclan/images/brazil.png" alt="" />
                        <p>PORTUGUÊS</p>
                    </Link>
                </li>
            </ul>
        </button>
    )
}
